import * as React from 'react';
import { Controller, CangjieInputEvent } from '@ali/4ever-cangjie';

export default function onCangjieInput(
  event: CangjieInputEvent,
  controller: Controller,
  next: Function,
) {
  const { value } = controller;
  const { data, endBlock } = value;

  // 暂定：高亮块整体选中时禁止输入
  if (data.selectedCallout && endBlock?.data?.calloutPr) {
    return controller;
  }

  return next();
}
